import Swiper from 'swiper';

const slider = (firstImg, secondImg, thridImg) => {
	const first = document.querySelector(firstImg);
	const second = document.querySelector(secondImg);
	const thrid = document.querySelector(thridImg);

	const firstImgUrl = '../images/sprites/png/first.png';
	const secondImgUrl = '../images/sprites/png/main.png';
	const thridImgUrl = '../images/sprites/png/second.png';

	function hellperSwapImg(elem, img) {
		elem.style.backgroundImage = `url(${img})`;
	}

	function swapImg() {
		if (mySwiper.activeIndex === 0) {
			hellperSwapImg(first, secondImgUrl);
			hellperSwapImg(second, firstImgUrl);
			hellperSwapImg(thrid, thridImgUrl);
		} else if (mySwiper.activeIndex === 2) {
			hellperSwapImg(first, firstImgUrl);
			hellperSwapImg(second, thridImgUrl);
			hellperSwapImg(thrid, secondImgUrl);
		} else if (mySwiper.activeIndex === 1) {
			hellperSwapImg(first, firstImgUrl);
			hellperSwapImg(second, secondImgUrl);
			hellperSwapImg(thrid, thridImgUrl);
		} else {
			console.log(`slide ${mySwiper.activeIndex} not found`)
		}
	}

	let mySwiper = new Swiper('.swiper-container', {
		initialSlide: 1,
		clickable: false,
	});
	mySwiper.on('slideChange', () => {
		swapImg();
	});
};

export default slider;
