import './vendor';
import './helpers';
import infoBlock from './modules/infoBlock';
import paralax from './modules/paralax';
import slider from './modules/slider';
import showMainMenu from './modules/showMainMenu';
import menu from './modules/menu';

document.addEventListener('DOMContentLoaded', () => {
	try {
		infoBlock('main');
	} catch (error) {
		console.log(`error init infoBlock ${error}`);
	}
	try {
		paralax('.slider__img-first', '.slider__img-second', '.slider__img-thrid');
	} catch (error) {
		console.log(`error init paralax ${error}`);
	}
	try {
		slider('.slider__img-first', '.slider__img-second', '.slider__img-thrid');
	} catch (error) {
		console.log(`error init slider ${error}`);
	}
	try {
		showMainMenu('main');
	} catch (error) {
		console.log(`error init show main menu ${error}`);
	}
	try {
		menu('.header__nav', '.header__nav-menu');
	} catch (error) {
		console.log(`error init menu ${error}`);
	}
});
