const paralax = (first, second, thrid) => {
	const firstImg = document.querySelector(first);
	const secondImg = document.querySelector(second);
	const thridImg = document.querySelector(thrid);

	function createPosition(x, y, element, numb1, numb2) {
		element.style.transform = `translate(-${x * numb1}px, -${y * numb2}px )`;
	}
	window.addEventListener('mousemove', (pos) => {
		let x = (pos.clientX / window.innerWidth).toFixed(4);
		let y = (pos.clientY / innerHeight).toFixed(4);
		createPosition(x, y, firstImg, 25, 31);
		createPosition(x, y, secondImg, 15, 21);
		createPosition(x, y, thridImg, 30, 42);
	});
};

export default paralax;
