const infoBlock = (block) => {
	const main = document.querySelector(block);
	const info = document.createElement('div');

	function showInfoBlock(pos) {
		info.style.cssText = `
			display: block;
			opacity: .8;
			left: ${pos.pageX + 20}px;
			top: ${pos.pageY - 20}px;
		`;
	}

	function createInfoBlock(pos) {
		document.body.append(info);
		info.classList.add('info');
		info.textContent = 'drag me';
		showInfoBlock(pos);
	}

	function hideInfoBlock() {
		info.style.cssText = `
			display: none;
			opacity: 0;
		`;
	}

	function init() {
		if (main.style.opacity !== '0' && main.style.opacity !== '') {
			main.addEventListener('mousemove', (position) => {
				createInfoBlock(position);
			});
			main.addEventListener('mouseleave', () => {
				hideInfoBlock();
			});
		} else {
			setTimeout(() => {
				init();
			}, 1000);
		}
	}
	init();
};

export default infoBlock;
